body {
  background: #4e54c8;
  background: -webkit-linear-gradient(to left, #8f94fb, #4e54c8);
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.search-form {
  background-color: #007bff;
  justify-content: center;
  margin: 0 auto 1rem;
  padding: .5rem;
  display: flex;
}

input[type="text"] {
  width: 25vw;
  min-width: 320px;
  border: 1px solid #fff;
  border-radius: 4px 0 0 4px;
  padding: .5rem;
  font-size: 1rem;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: 1px solid #fff;
  border-radius: 0 4px 4px 0;
  padding: .5rem 1rem;
  font-size: 1rem;
}

input[type="text"]:focus {
  outline-offset: -2px;
  outline-color: #007bff;
}

button.load-more {
  width: 5rem;
  height: 5rem;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: 1px solid #fff;
  border-radius: 50%;
  margin: 0 auto 2rem;
  padding: .5rem 1rem;
  font-size: 1.2rem;
  font-weight: 600;
  transition: transform .2s cubic-bezier(.4, 0, 1, 1);
  display: none;
}

button.load-more:hover {
  transform: scale(1.5);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  padding-bottom: 1rem;
  display: flex;
}

.gallery a:hover img {
  transition: transform .2s;
  transform: scale(1.3);
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 4px;
  flex-direction: column;
  flex-basis: calc(25% - 3rem);
  justify-content: center;
  align-items: center;
  padding: .5rem;
  display: flex;
}

.photo-card img {
  max-width: 100%;
  height: 30vh;
  border-radius: 4px;
}

.info {
  width: 98%;
  text-align: center;
  background-color: #ccc;
  border-radius: 4px;
  justify-content: space-around;
  gap: 2rem;
  margin-top: .5rem;
  padding: .5rem;
  display: flex;
}

.info-item {
  border-radius: 4px;
  margin: 0;
  font-size: 1rem;
}

.circles {
  width: 90%;
  height: 98%;
  z-index: -10;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.circles li {
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, .2);
  list-style: none;
  animation: animate 25s linear infinite;
  display: block;
  position: absolute;
  bottom: -150px;
}

.circles li:nth-child(1) {
  width: 80px;
  height: 80px;
  animation-delay: 0s;
  left: 25%;
}

.circles li:nth-child(2) {
  width: 20px;
  height: 20px;
  animation-duration: 12s;
  animation-delay: 2s;
  left: 10%;
}

.circles li:nth-child(3) {
  width: 20px;
  height: 20px;
  animation-delay: 4s;
  left: 70%;
}

.circles li:nth-child(4) {
  width: 60px;
  height: 60px;
  animation-duration: 18s;
  animation-delay: 0s;
  left: 40%;
}

.circles li:nth-child(5) {
  width: 20px;
  height: 20px;
  animation-delay: 0s;
  left: 65%;
}

.circles li:nth-child(6) {
  width: 110px;
  height: 110px;
  animation-delay: 3s;
  left: 75%;
}

.circles li:nth-child(7) {
  width: 150px;
  height: 150px;
  animation-delay: 7s;
  left: 35%;
}

.circles li:nth-child(8) {
  width: 25px;
  height: 25px;
  animation-duration: 45s;
  animation-delay: 15s;
  left: 50%;
}

.circles li:nth-child(9) {
  width: 15px;
  height: 15px;
  animation-duration: 35s;
  animation-delay: 2s;
  left: 20%;
}

.circles li:nth-child(10) {
  width: 150px;
  height: 150px;
  animation-duration: 11s;
  animation-delay: 0s;
  left: 85%;
}

@keyframes animate {
  0% {
    opacity: 1;
    border-radius: 0;
    transform: translateY(0)rotate(0deg);
  }

  100% {
    opacity: 0;
    border-radius: 50%;
    transform: translateY(-1000px)rotate(720deg);
  }
}

.loader {
  width: 150px;
  height: 150px;
  z-index: 99;
  border: 3px solid rgba(0, 0, 0, 0);
  border-top-color: #16a085;
  border-radius: 50%;
  margin: -75px 0 0 -75px;
  animation: spin 1.7s linear infinite;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader:before {
  content: "";
  border: 3px solid rgba(0, 0, 0, 0);
  border-top-color: #e74c3c;
  border-radius: 50%;
  animation: spin-reverse .7s linear infinite;
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
}

.loader:after {
  content: "";
  border: 3px solid rgba(0, 0, 0, 0);
  border-top-color: #f9c922;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  position: absolute;
  top: 15px;
  bottom: 15px;
  left: 15px;
  right: 15px;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes spin-reverse {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(-360deg);
  }
}

@keyframes spin-reverse {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

